@media screen and (min-width: 701px) {
  #request-info-button {
    font-size: 22px;
    color: #2665C4;
    cursor: pointer;
    height: 50px;
    line-height: 22px;
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
    width: 100%;
    background-color: white;
    border-radius: 5px;
    border-color: #2665C4;
    border-style: solid;
    border-width: 2px; } }

@media screen and (max-width: 700px) {
  #request-info-button {
    font-size: 22px;
    color: #2665C4;
    cursor: pointer;
    width: 100%;
    height: 60px;
    background-color: white;
    border-radius: 5px;
    border-color: #2665C4;
    border-style: solid;
    border-width: 2px; } }
